import React, { useContext, useEffect, useMemo, useState, useRef, useCallback } from 'react';
import { supabase } from '../supabaseClient';
import AccountingExpenseModal from '../components/AccountingExpenseModal.jsx';
import { AuthContext } from '../App.jsx';
import { BadgeDollarSign, LogIn, LogOut, Users, CalendarDays, PlusCircle, MinusCircle, Info, Bell, ShieldCheck, Loader2 } from 'lucide-react';

export default function ReceptionDashboard() {
  // Manager handover modal state/hooks
  const [showManagerHandoverModal, setShowManagerHandoverModal] = useState(false);
  // setManagerHandoverData is reserved for future updates; suppress unused-var warning
  // eslint-disable-next-line no-unused-vars
  const [managerHandoverData, setManagerHandoverData] = useState(null);
  const managerHandoverAmountRef = useRef();
    //        ( )
    const handleDeleteClosedShifts = async () => {
      const todayStr = new Date().toISOString().slice(0, 10);
      if (!window.confirm('         ')) return;
      try {
        const { error } = await supabase
          .from('reception_shifts')
          .delete()
          .eq('shift_date', todayStr)
          .eq('status', 'closed');
        if (error) throw error;
        alert('       .');
      } catch (e) {
        alert('   : ' + (e.message || e));
      }
    };
  const currentUser = useContext(AuthContext);
  const [loading, setLoading] = useState(true);
  const [date] = useState(() => new Date().toISOString().slice(0, 10));
  const [reservations, setReservations] = useState([]);
  // eslint-disable-next-line no-unused-vars
  const [upcoming, _setUpcoming] = useState([]);
  const [search, setSearch] = useState('');
  // pendingTx kept for later use; suppress unused-var warning
  // eslint-disable-next-line no-unused-vars
  const [pendingTx, setPendingTx] = useState([]);
  const [showExpense, setShowExpense] = useState(false);
  const [showHandoverModal, setShowHandoverModal] = useState(false);
  const [handoverType, setHandoverType] = useState('manager');
  const [handoverAmount, setHandoverAmount] = useState(0);
  const [handoverRecipientId, setHandoverRecipientId] = useState(null);
  const [handoverStaffList, setHandoverStaffList] = useState([]);
  const [handoverManagers, setHandoverManagers] = useState([]);
  const [handoverLoading, setHandoverLoading] = useState(false);
  const [pendingReceipts, setPendingReceipts] = useState([]);
  const [showPendingReceiptModal, setShowPendingReceiptModal] = useState(false);
  const [pendingTotal, setPendingTotal] = useState(0);
  const [actualReceivedAmount, setActualReceivedAmount] = useState(0);
  const [currentShift, setCurrentShift] = useState(null);
  const currentShiftRef = useRef(null);
  const [shiftStats, setShiftStats] = useState({
    cashIncome: 0,
    cashExpense: 0,
    cashNet: 0,
    instapayIncome: 0,
    instapayExpense: 0,
    walletIncome: 0,
    walletExpense: 0,
    bankIncome: 0,
    bankExpense: 0,
    nonCashIncome: 0,
    nonCashExpense: 0,
  });
  const [deliveredThisShift, setDeliveredThisShift] = useState(0);
  const [dailySummary, setDailySummary] = useState({ received: 0, delivered: 0, net: 0 });
  // TODO:   autoShiftEnabled     
    // const [autoShiftEnabled, setAutoShiftEnabled] = useState(false);
  // readOnly state currently controlled but not read in this component
  // eslint-disable-next-line no-unused-vars
  const [readOnly, setReadOnly] = useState(false);

  // UI Components
  const KPICard = ({ icon, label, value, color }) => (
    <div className={`flex items-center gap-3 bg-white rounded-xl shadow p-4 transition hover:scale-105 border-t-4 ${color}`}>
      <div className="bg-gray-100 rounded-full p-2">{icon}</div>
      <div>
        <div className="text-lg font-bold">{value}</div>
        <div className="text-xs text-gray-500">{label}</div>
      </div>
    </div>
  );

  const QuickAction = ({ icon, label, onClick, disabled, tooltip }) => (
    <button
      className={`flex flex-col items-center justify-center gap-1 bg-gradient-to-tr from-blue-100 to-blue-50 hover:from-blue-200 hover:to-blue-100 rounded-lg p-3 shadow transition disabled:opacity-50 relative`}
      onClick={onClick}
      disabled={disabled}
      title={tooltip}
      type="button"
    >
      <span className="text-blue-600">{icon}</span>
      <span className="text-xs font-medium">{label}</span>
      {disabled && (
        <span className="absolute -top-2 -left-2 bg-red-500 text-white text-[10px] px-2 py-0.5 rounded-full shadow"></span>
      )}
    </button>
  );

  
  
  //        (    hooks)
  const updateShiftStats = useCallback(async (shiftParam) => {
    const shift = shiftParam || currentShiftRef.current;
    if (!shift || !currentUser?.id) {
      setShiftStats({
        cashIncome: 0,
        cashExpense: 0,
        cashNet: 0,
        instapayIncome: 0,
        instapayExpense: 0,
        walletIncome: 0,
        walletExpense: 0,
        bankIncome: 0,
        bankExpense: 0,
        nonCashIncome: 0,
        nonCashExpense: 0,
      });
      return;
    }
    try {
      let txRes;
      if (shift.id) {
        txRes = await supabase
          .from('accounting_transactions')
          .select('direction,amount,payment_method')
          .eq('reception_shift_id', shift.id);
      } else {
        txRes = await supabase
          .from('accounting_transactions')
          .select('direction,amount,payment_method')
          .eq('tx_date', shift.shift_date)
          .eq('created_by', currentUser.id);
      }
      const { data: txs, error } = txRes;
      if (error) throw error;
      let cashInc = 0, cashExp = 0;
      let instaInc = 0, instaExp = 0;
      let walletInc = 0, walletExp = 0;
      let bankInc = 0, bankExp = 0;
      (txs || []).forEach((t) => {
        const a = Number(t.amount || 0);
        if (!a) return;
        const dirInc = t.direction === 'income';
        const pm = (t.payment_method || '').toLowerCase();
        if (pm === 'cash') {
          if (dirInc) cashInc += a; else cashExp += a;
        } else if (pm === 'instapay') {
          if (dirInc) instaInc += a; else instaExp += a;
        } else if (pm === 'bank') {
          if (dirInc) bankInc += a; else bankExp += a;
        } else { // other wallets
          if (dirInc) walletInc += a; else walletExp += a;
        }
      });
      const nonCashInc = instaInc + walletInc + bankInc;
      const nonCashExp = instaExp + walletExp + bankExp;
      setShiftStats({
        cashIncome: Math.round(cashInc),
        cashExpense: Math.round(cashExp),
        cashNet: Math.round(cashInc - cashExp),
        instapayIncome: Math.round(instaInc),
        instapayExpense: Math.round(instaExp),
        walletIncome: Math.round(walletInc),
        walletExpense: Math.round(walletExp),
        bankIncome: Math.round(bankInc),
        bankExpense: Math.round(bankExp),
        nonCashIncome: Math.round(nonCashInc),
        nonCashExpense: Math.round(nonCashExp),
      });
      // deliveredThisShift handled below
    } catch (e) {
      console.error('updateShiftStats error', e);
    }
    try {
      if (shift && shift.id) {
        const { data: delRows } = await supabase.from('reception_shift_handovers').select('amount').eq('from_shift_id', shift.id);
        const delivered = (delRows || []).reduce((a, r) => a + Number(r.amount || 0), 0);
        setDeliveredThisShift(delivered);
      } else setDeliveredThisShift(0);
    } catch (e) {
      console.error('fetch deliveredThisShift error', e);
      setDeliveredThisShift(0);
    }
  }, [currentUser]);

  const fetchDailySummary = useCallback(async () => {
    if (!currentUser?.id) return;
    try {
      const { data: myShifts } = await supabase.from('reception_shifts').select('id').eq('staff_user_id', currentUser.id).eq('shift_date', (new Date().getFullYear()+'-'+String(new Date().getMonth()+1).padStart(2,'0')+'-'+String(new Date().getDate()).padStart(2,'0')));
      const myShiftIds = (myShifts || []).map(s => s.id).filter(Boolean);
      let delivered = 0;
      if (myShiftIds.length > 0) {
        const { data: delRows } = await supabase.from('reception_shift_handovers').select('amount').in('from_shift_id', myShiftIds);
        delivered = (delRows || []).reduce((a, r) => a + Number(r.amount || 0), 0);
      }
      let received = 0;
      if (myShiftIds.length > 0) {
        const { data: r1 } = await supabase.from('reception_shift_handovers').select('amount').in('to_shift_id', myShiftIds);
        received += (r1 || []).reduce((a, r) => a + Number(r.amount || 0), 0);
      }
      const { data: r2 } = await supabase.from('reception_shift_handovers').select('amount').eq('to_staff_user_id', currentUser.id).neq('status', 'pending');
      received += (r2 || []).reduce((a, r) => a + Number(r.amount || 0), 0);
      setDailySummary({ received: Math.round(received), delivered: Math.round(delivered), net: Math.round(received - delivered) });
    } catch (e) {
      console.error('fetchDailySummary error', e);
      setDailySummary({ received: 0, delivered: 0, net: 0 });
    }
  }, [currentUser]);


  useEffect(() => {
    async function load() {
      setLoading(true);
      try {
        const { data: todayRows } = await supabase
          .from('reservations_overview')
          .select('*')
          .or(
            `check_in_date.eq.${date},check_out_date.eq.${date},is_current.eq.true`
          );

        setReservations(todayRows || []);
      } catch (e) {
        console.error(e);
      } finally {
        setLoading(false);
      }
    }
    load();
    //      
    const fetchCurrentShift = async () => {
      try {
        const todayStr = new Date().toISOString().slice(0, 10);
        const { data: shifts } = await supabase
          .from('reception_shifts')
          .select('id,status,shift_date,staff_user_id,opened_at,closed_at,opening_cash,closing_cash,opening_note,closing_note,short_code')
          .eq('staff_user_id', currentUser?.id)
          .eq('shift_date', todayStr)
          .in('status', ['open','closed'])
          .order('opened_at', { ascending: false })
          .limit(1);
        const shift = (shifts && shifts.length > 0) ? shifts[0] : null;
        setCurrentShift(shift);
        currentShiftRef.current = shift;
        setReadOnly(!shift || shift.status !== 'open');
        //    
        if (shift) updateShiftStats(shift);
      } catch (e) {
        console.error('fetchCurrentShift error', e);
      }
    };
    fetchCurrentShift();
  }, [date, currentUser?.id, updateShiftStats]);

  useEffect(() => {
    currentShiftRef.current = currentShift;
  }, [currentShift]);

  //           
  useEffect(() => {
    const checkPendings = async () => {
      try {
        if (!currentUser?.id) return;
        const { data: pendings } = await supabase
          .from('reception_shift_handovers')
          .select('*')
          .is('to_shift_id', null)
          .eq('to_staff_user_id', currentUser.id)
          .in('status', ['pending', 'sent_to_staff']);
        if (pendings && pendings.length > 0) {
          const fromShiftIds = [...new Set(pendings.map(p => p.from_shift_id).filter(Boolean))];
          const { data: fromShifts } = await supabase.from('reception_shifts').select('id,staff_user_id').in('id', fromShiftIds);
          const senderIds = [...new Set((fromShifts || []).map(s => s.staff_user_id).filter(Boolean))];
          const { data: senders } = await supabase.from('staff_users').select('id,full_name').in('id', senderIds);
          const senderMap = {};
          (senders || []).forEach(s => { senderMap[s.id] = s.full_name; });
          const enhanced = (pendings || []).map(p => {
            const fs = (fromShifts || []).find(f => String(f.id) === String(p.from_shift_id));
            return {
              ...p,
              sender_id: fs?.staff_user_id || null,
              sender_name: senderMap[fs?.staff_user_id] || '  '
            };
          });
          const total = (enhanced || []).reduce((acc, x) => acc + Number(x.amount || 0), 0);
          setPendingReceipts(enhanced);
          setPendingTotal(Math.round(total));
          setActualReceivedAmount(Math.round(total));
          setShowPendingReceiptModal(true);
        }
      } catch (e) {
        console.error('check pending handovers error', e);
      }
    };
    checkPendings();
  }, [currentUser?.id, currentShift?.id]);



  //     
  useEffect(() => {
    const handler = () => updateShiftStats();
    window.addEventListener('accounting-tx-updated', handler);
    return () => { try { window.removeEventListener('accounting-tx-updated', handler); } catch(_){} };
  }, [updateShiftStats]);

  // refresh daily summary when user or shift changes
  useEffect(() => {
    fetchDailySummary();
  }, [fetchDailySummary, currentShift?.id]);

  const todayLabel = useMemo(() => {
    try {
      return new Date().toLocaleDateString('ar-EG', {
        weekday: 'long',
        day: 'numeric',
        month: 'long',
        year: 'numeric',
      });
    } catch {
      return '';
    }
  }, []);

  const filteredToday = useMemo(() => {
    const term = search.trim().toLowerCase();
    if (!term) return reservations;
    return reservations.filter((r) => {
      return (
        (r.guest_name || '').toLowerCase().includes(term) ||
        (r.guest_phone || '').toLowerCase().includes(term) ||
        String(r.room_label || r.room_id || '').toLowerCase().includes(term)
      );
    });
  }, [reservations, search]);

  const arrivalsToday = useMemo(
    () =>
      filteredToday.filter(
        (r) =>
          r.check_in_date === date &&
          (r.status === 'pending' || r.status === 'confirmed')
      ),
    [filteredToday, date]
  );

  const departuresToday = useMemo(
    () =>
      filteredToday.filter(
        (r) => r.check_out_date === date && r.status === 'checked_in'
      ),
    [filteredToday, date]
  );

  const inhouse = useMemo(
    () => filteredToday.filter((r) => r.is_current && r.status === 'checked_in'),
    [filteredToday]
  );

  const upcoming3Days = useMemo(
    () => (upcoming || []).filter((r) => !r.is_past),
    [upcoming]
  );

  const statusBadge = (status) => {
    const map = {
      pending: { text: ' ', cls: 'bg-yellow-100 text-yellow-800' },
      confirmed: { text: '', cls: 'bg-emerald-100 text-emerald-800' },
      checked_in: { text: ' ', cls: 'bg-blue-100 text-blue-800' },
      checked_out: { text: ' ', cls: 'bg-gray-100 text-gray-800' },
      cancelled: { text: '', cls: 'bg-red-100 text-red-800' },
      no_show: { text: ' ', cls: 'bg-orange-100 text-orange-800' },
    };
    const v = map[status] || {
      text: status || '-',
      cls: 'bg-slate-100 text-slate-700',
    };
    return (
      <span
        className={`px-2 py-0.5 rounded-full text-[11px] font-medium ${v.cls}`}
      >
        {v.text}
      </span>
    );
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gray-50" dir="rtl">
        <Loader2 className="animate-spin w-6 h-6 text-blue-400 mx-2" />
        <div className="text-gray-500 text-sm">    ...</div>
      </div>
    );
  }

  const handleOpenShift = async () => {
    if (!currentUser?.id) {
      alert('      .');
      return;
    }
    if (currentShift?.status === 'open') {
      alert('   .');
      return;
    }
    try {
      //         
      const { data: myOpen } = await supabase
        .from('reception_shifts')
        .select('id,status')
        .eq('staff_user_id', currentUser.id)
        .eq('status', 'open')
        .limit(1);
      if (myOpen && myOpen.length > 0) {
        alert('           .        .');
        return;
      }
      //  RPC         
      //   v2   short_code      v1 
      let rpcData, rpcError;
      {
        const resV2 = await supabase.rpc('open_reception_shift_if_allowed_v2', {
          p_shift_date: date,
          p_staff_user_id: currentUser.id,
        });
        if (resV2 && !resV2.error) {
          rpcData = resV2.data;
          rpcError = null;
        } else {
          const resV1 = await supabase.rpc('open_reception_shift_if_allowed', {
            p_shift_date: date,
            p_staff_user_id: currentUser.id,
          });
          rpcData = resV1.data;
          rpcError = resV1.error;
        }
      }
      if (rpcError) {
        const friendly = rpcError.message || rpcError.details || '  ';
        if (rpcError.code === 'P0001') {
          alert(friendly);
        } else {
          console.error('Supabase RPC error:', rpcError);
          alert('  : ' + friendly);
        }
        return;
      }
      const shift = Array.isArray(rpcData) && rpcData.length > 0 ? rpcData[0] : null;
      setCurrentShift(shift);
      setReadOnly(!shift);
      setShiftStats({ cashIncome: 0, cashExpense: 0, net: 0 });
      //       handovers   
      if (shift && shift.id) {
        try {
          //  handovers  to_shift_id = 0      note
          const todayStr = new Date().toISOString().slice(0, 10);
          console.debug('open shift debug', { shift, currentUserId: currentUser?.id, todayStr });
          const { data: pendings } = await supabase
            .from('reception_shift_handovers')
            .select('*')
            .is('to_shift_id', null)
            .eq('to_staff_user_id', currentUser.id)
            .in('status', ['pending', 'sent_to_staff']);
          console.debug('pending handovers fetched on open:', pendings);
          if (pendings && pendings.length > 0) {
            //       
            const fromShiftIds = [...new Set(pendings.map(p => p.from_shift_id).filter(Boolean))];
            const { data: fromShifts } = await supabase.from('reception_shifts').select('id,staff_user_id').in('id', fromShiftIds);
            const senderIds = [...new Set((fromShifts || []).map(s => s.staff_user_id).filter(Boolean))];
            const { data: senders } = await supabase.from('staff_users').select('id,full_name').in('id', senderIds);
            const senderMap = {};
            (senders || []).forEach(s => { senderMap[s.id] = s.full_name; });
            const enhanced = (pendings || []).map(p => {
              const fs = (fromShifts || []).find(f => String(f.id) === String(p.from_shift_id));
              return {
                ...p,
                sender_id: fs?.staff_user_id || null,
                sender_name: senderMap[fs?.staff_user_id] || '  '
              };
            });
            const total = (enhanced || []).reduce((acc, x) => acc + Number(x.amount || 0), 0);
            setPendingReceipts(enhanced);
            setPendingTotal(Math.round(total));
            setActualReceivedAmount(Math.round(total));
            setShowPendingReceiptModal(true);
          }
        } catch (e) {
          console.error('link pending handovers error', e);
        }
      }
      alert(shift ? '   ' : '  ');
    } catch (e) {
      alert(e.message);
    }
  };

  const handleCloseShift = async () => {
    if (!currentShift || currentShift.status !== 'open') {
      alert('    ');
      return;
    }
    //      ()
    try {
      //       
      const [{ data: staff }, { data: managers }] = await Promise.all([
        supabase.from('staff_users').select('id,full_name').eq('role', 'reception'),
        supabase.from('staff_users').select('id,full_name').eq('role', 'manager'),
      ]);
      setHandoverStaffList(staff || []);
      setHandoverManagers(managers || []);
      //   =    ( +       )
      const expectedDrawerCash = ((currentShift?.opening_cash || 0) + (shiftStats.cashNet || 0)) - (deliveredThisShift || 0);
      setHandoverAmount(Math.max(0, Math.round(expectedDrawerCash)));
      setHandoverType('manager');
      setHandoverRecipientId((managers && managers[0] && managers[0].id) || null);
      setShowHandoverModal(true);
    } catch (e) {
      console.error('open handover modal error', e);
      alert('   : ' + (e.message || e));
    }
  };

  const submitHandoverAndClose = async () => {
    if (!currentShift) return;
    if (!(handoverAmount >= 0)) {
      alert('   .');
      return;
    }
    const roundedAmount = Math.round(handoverAmount || 0);
    if (handoverType === 'manager' && !handoverRecipientId) {
      alert('   .');
      return;
    }
    if (handoverType === 'staff' && !handoverRecipientId) {
      alert('    .');
      return;
    }
    setHandoverLoading(true);
    try {
      //  
      const { error: closeErr } = await supabase.from('reception_shifts').update({ status: 'closed', closed_at: new Date().toISOString(), closing_cash: roundedAmount, counted_cash: roundedAmount }).eq('id', currentShift.id);
      if (closeErr) throw closeErr;

      //  handover
      const payload = {
        from_shift_id: currentShift.id,
        amount: roundedAmount,
        tx_date: currentShift.shift_date,
        note: handoverType === 'staff' ? '    ' : '  ',
        created_by: currentUser?.id || null,
      };
      if (handoverType === 'manager') {
        payload.to_manager_id = handoverRecipientId;
      } else {
        // create pending by leaving to_shift_id NULL and set to_staff_user_id
        payload.to_shift_id = null;
        payload.to_staff_user_id = handoverRecipientId;
      }
      const { data: handData, error: handErr } = await supabase.from('reception_shift_handovers').insert(payload).select('*');
      if (handErr) throw handErr;
      console.debug('handover inserted:', handData);
      //        received_by_manager  
      if (handoverType === 'manager' && handData && handData[0]) {
        const hand = handData[0];
        try {
          await supabase.from('reception_shift_handovers').update({ status: 'received_by_manager', received_by: handoverRecipientId, received_at: new Date().toISOString() }).eq('id', hand.id);
          //     
          const { data: fromRows } = await supabase.from('reception_shifts').select('id,closing_note').eq('id', currentShift.id).limit(1);
          const fromRow = (fromRows && fromRows.length > 0) ? fromRows[0] : null;
          const { data: mgr } = await supabase.from('staff_users').select('full_name').eq('id', handoverRecipientId).limit(1);
          const mgrName = (mgr && mgr[0] && mgr[0].full_name) || '';
          const noteLine = `   ${roundedAmount} .   ${mgrName}`;
          const newNote = ((fromRow && fromRow.closing_note) ? (fromRow.closing_note + '\n' + noteLine) : noteLine);
          await supabase.from('reception_shifts').update({ closing_note: newNote }).eq('id', currentShift.id);
          //        ()  
          await supabase.from('accounting_transactions').insert({
            tx_date: new Date().toISOString().slice(0, 10),
            direction: 'expense',
            category_id: null,
            amount: roundedAmount,
            payment_method: 'cash',
            bank_account_id: null,
            source_type: 'reception_shift',
            reservation_id: null,
            description: `     ${currentShift.id}`,
            status: 'confirmed',
            reception_shift_id: currentShift.id,
            created_by: currentUser?.id || null,
            delivered_in_handover_id: hand.id,
          });
          //        
          await supabase
            .from('accounting_transactions')
            .update({ status: 'confirmed', confirmed_at: new Date().toISOString(), confirmed_by: handoverRecipientId, delivered_in_handover_id: hand.id })
            .eq('reception_shift_id', currentShift.id)
            .eq('payment_method', 'cash')
            .eq('status', 'pending');
        } catch (e) {
          console.error('error marking handover received by manager', e);
        }
      }

      //  :     
      const { data: shifts } = await supabase.from('reception_shifts').select('id,status,opening_cash,closing_cash,closed_at,staff_user_id').eq('staff_user_id', currentUser.id).eq('shift_date', (new Date().getFullYear()+'-'+String(new Date().getMonth()+1).padStart(2,'0')+'-'+String(new Date().getDate()).padStart(2,'0'))).eq('status', 'closed').limit(1);
      const closedShift = (shifts && shifts.length > 0) ? shifts[0] : null;
      setCurrentShift(closedShift);
      setReadOnly(true);
      setShowHandoverModal(false);
      //    
      updateShiftStats(closedShift);
      alert('     .');
    } catch (e) {
      console.error('submitHandover error', e);
      alert('  / : ' + (e.message || e));
    } finally {
      setHandoverLoading(false);
    }
  };

  const confirmPendingReceipts = async () => {
    if (!currentShift) return;
    setHandoverLoading(true);
    try {
      const toUpdate = pendingReceipts || [];
      let expectedTotal = 0;
      for (const p of toUpdate) {
        expectedTotal += Number(p.amount || 0);
        //       
        await supabase.from('reception_shift_handovers').update({ to_shift_id: currentShift.id, to_staff_user_id: null, status: 'received_by_staff', received_by: currentUser.id, received_at: new Date().toISOString() }).eq('id', p.id);
        //          
        try {
          const { data: fromRows } = await supabase.from('reception_shifts').select('id,closing_note').eq('id', p.from_shift_id).limit(1);
          const fromRow = (fromRows && fromRows.length > 0) ? fromRows[0] : null;
          let receiverName = currentUser?.full_name || '';
          if (!receiverName) {
            const { data: me } = await supabase.from('staff_users').select('full_name').eq('id', currentUser.id).limit(1);
            receiverName = (me && me[0] && me[0].full_name) || receiverName;
          }
          const noteLine = `   ${Math.round(p.amount || 0)} .  ${receiverName}  ${new Date().toLocaleString('ar-EG')}`;
          const newNote = ((fromRow && fromRow.closing_note) ? (fromRow.closing_note + '\n' + noteLine) : noteLine);
          await supabase.from('reception_shifts').update({ closing_note: newNote }).eq('id', p.from_shift_id);
        } catch (e) {
          console.error('failed updating from shift note', e);
        }
        // :            
      }
      const actual = Math.round(actualReceivedAmount || 0);
      const expected = Math.round(expectedTotal || 0);
      const diff = actual - expected; //  =   = 

      //     opening_cash  
      const { error: updErr } = await supabase.from('reception_shifts').update({ opening_cash: (currentShift.opening_cash || 0) + actual, opening_note: ((currentShift.opening_note || '') + (currentShift.opening_note ? '\n' : '') + `  :  ${expected} .  ${actual} .  ${diff} .`) }).eq('id', currentShift.id);
      if (updErr) throw updErr;

      //           
      if (diff !== 0) {
        const isSurplus = diff > 0;
        const note = isSurplus
          ? `    :  ${expected} .  ${actual} .  ${diff} ..`
          : `    :  ${expected} .  ${actual} .  ${Math.abs(diff)} ..`;
        await supabase.from('accounting_transactions').insert({
          tx_date: new Date().toISOString().slice(0, 10),
          direction: isSurplus ? 'income' : 'expense',
          category_id: null,
          amount: Math.abs(diff),
          payment_method: 'cash',
          bank_account_id: null,
          source_type: 'reception_shift',
          reservation_id: null,
          description: note,
          status: 'confirmed',
          reception_shift_id: currentShift.id,
          created_by: currentUser.id,
        });
      }
      //    
      updateShiftStats({ ...currentShift, opening_cash: (currentShift.opening_cash || 0) + actual });
      setShowPendingReceiptModal(false);
      setPendingReceipts([]);
      setPendingTotal(0);
      setActualReceivedAmount(0);
      alert('        .');
    } catch (e) {
      console.error('confirmPendingReceipts error', e);
      alert('   : ' + (e.message || e));
    } finally {
      setHandoverLoading(false);
    }
  };

  //  
  const shiftOpen = currentShift?.status === 'open';

  return (
    <div className="flex flex-col gap-8 p-6 bg-gray-50 min-h-screen" dir="rtl">
      {/* Header */}
      <div className="flex flex-col md:flex-row md:justify-between md:items-center gap-4">
        <div className="flex items-center gap-3">
          <ShieldCheck className="text-blue-500" />
          <span className="text-xl font-bold text-gray-700">  </span>
          <span className="ml-2 px-2 py-0.5 rounded bg-blue-100 text-blue-700 text-xs">{todayLabel}</span>
        </div>
        <div className="flex gap-2">
          <button className="bg-yellow-400 text-white px-4 py-2 rounded shadow hover:bg-yellow-500 transition" title=" ">
            <Info className="inline w-4 h-4 mr-1" />  
          </button>
          <button className="bg-white border rounded p-2 shadow hover:bg-gray-100 transition" title="">
            <Bell className="w-5 h-5 text-blue-400" />
          </button>
          {/*        */}
          {currentUser?.role === 'manager' && (
            <button
              className="bg-red-600 text-white px-4 py-2 rounded shadow hover:bg-red-700 transition"
              title="     "
              onClick={handleDeleteClosedShifts}
            >
                
            </button>
          )}
        </div>
      </div>

      {/* KPI Cards */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <KPICard icon={<LogIn className="w-6 h-6 text-green-500" />} label=" " value={arrivalsToday.length} color="border-green-400" />
        <KPICard icon={<LogOut className="w-6 h-6 text-red-500" />} label=" " value={departuresToday.length} color="border-red-400" />
        <KPICard icon={<Users className="w-6 h-6 text-blue-500" />} label="" value={inhouse.length} color="border-blue-400" />
        <KPICard icon={<CalendarDays className="w-6 h-6 text-yellow-500" />} label="" value={upcoming3Days.length} color="border-yellow-400" />
      </div>

      {/* Quick Actions */}
      <div className="flex flex-wrap gap-4">
        <QuickAction
          icon={<BadgeDollarSign className="w-6 h-6" />}
          label=""
          onClick={() => alert('   ')}
          disabled={!shiftOpen}
          tooltip={shiftOpen ? ' ' : '   '}
        />
        <QuickAction
          icon={<MinusCircle className="w-6 h-6" />}
          label=""
          onClick={() => setShowExpense(true)}
          disabled={!shiftOpen}
          tooltip={shiftOpen ? ' ' : '   '}
        />
        <QuickAction
          icon={<PlusCircle className="w-6 h-6" />}
          label=" "
          onClick={() => alert(' ')}
          disabled={!shiftOpen}
          tooltip={shiftOpen ? '  ' : '   '}
        />
      </div>

      {/* Shift Summary */}
      <div className="bg-white rounded-xl shadow p-4 flex flex-col md:flex-row md:items-center gap-4 border-t-4 border-blue-200">
        <div className="flex-1 flex flex-col gap-2">
          <div className="flex items-center gap-2">
            <ShieldCheck className="w-5 h-5 text-blue-400" />
            <span className="font-bold text-gray-700">  </span>
            {shiftOpen ? (
              <span className="ml-2 px-2 py-0.5 rounded bg-green-100 text-green-700 text-xs"></span>
            ) : (
              <span className="ml-2 px-2 py-0.5 rounded bg-red-100 text-red-700 text-xs"></span>
            )}
            {currentShift?.short_code && (
              <span className="ml-2 px-2 py-0.5 rounded bg-blue-50 text-blue-700 text-[11px]"> #{currentShift.short_code}</span>
            )}
          </div>
          <div className="flex gap-4 text-sm text-gray-600">
            <div> : <span className="font-bold text-green-600">{shiftStats.cashIncome} .</span></div>
            <div> : <span className="font-bold text-red-600">{shiftStats.cashExpense} .</span></div>
            <div> : <span className="font-bold text-blue-600">{shiftStats.cashNet} .</span></div>
          </div>
          <div className="mt-1 text-xs text-gray-600">
               (  ): <span className="font-bold">{shiftStats.nonCashIncome} .</span>
            <span className="ml-2"> : {shiftStats.instapayIncome}  : {shiftStats.walletIncome}  : {shiftStats.bankIncome}</span>
          </div>
            <div className="mt-3 grid grid-cols-1 md:grid-cols-3 gap-3 text-sm text-gray-700">
              <div className="bg-gray-50 p-3 rounded">
                <div className="text-xs text-gray-500">    ( )</div>
                <div className="font-bold text-lg">{((currentShift?.opening_cash || 0) + shiftStats.cashNet) - deliveredThisShift} .</div>
                <div className="text-[11px] text-gray-500">:  {Math.round(currentShift?.opening_cash || 0)} +   {shiftStats.cashNet}   {Math.round(deliveredThisShift || 0)}</div>
              </div>
              <div className="bg-gray-50 p-3 rounded">
                <div className="text-xs text-gray-500">     </div>
                <div className="font-bold text-lg">{deliveredThisShift} .</div>
              </div>
              <div className="bg-gray-50 p-3 rounded">
                <div className="text-xs text-gray-500">    ()</div>
                <div className="font-bold text-lg">{dailySummary.net} .</div>
                <div className="text-xs text-gray-500"> : {dailySummary.received} .   : {dailySummary.delivered} .</div>
              </div>
            </div>
            {currentShift?.opening_note && (
              <div className="mt-2 text-sm text-gray-700">  : <div className="text-sm font-medium">{currentShift.opening_note}</div></div>
            )}
            {currentShift?.closing_note && (
              <div className="mt-2 text-sm text-gray-700"> : <div className="text-sm font-medium whitespace-pre-line">{currentShift.closing_note}</div></div>
            )}
        </div>
        <div className="flex gap-2">
          {!shiftOpen && (
            <button
              onClick={handleOpenShift}
              className="bg-green-500 text-white px-4 py-2 rounded shadow hover:bg-green-600 transition"
            >
              <LogIn className="inline w-4 h-4 ml-1" />  
            </button>
          )}
          {shiftOpen && (
            <button
              onClick={handleCloseShift}
              className="bg-red-500 text-white px-4 py-2 rounded shadow hover:bg-red-600 transition"
            >
              <LogOut className="inline w-4 h-4 ml-1" />  
            </button>
          )}
        </div>
      </div>

      {/* Tables */}
      <div className="bg-white rounded-xl shadow p-4 border-t-4 border-gray-200">
        <div className="flex items-center justify-between mb-4">
          <div className="font-bold text-gray-700 flex items-center gap-2">
            <Users className="w-5 h-5 text-blue-400" />
             
            <span className="ml-2 bg-blue-100 text-blue-700 px-2 py-0.5 rounded text-xs">{filteredToday.length}</span>
          </div>
          <input
            className="border rounded px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-blue-200 transition"
            placeholder="   ..."
            value={search}
            onChange={e => setSearch(e.target.value)}
            style={{ minWidth: 180 }}
          />
        </div>
        <div className="overflow-x-auto">
          <table className="min-w-full text-sm text-right">
            <thead>
              <tr className="bg-gray-100">
                <th className="px-2 py-1"></th>
                <th className="px-2 py-1"></th>
                <th className="px-2 py-1"></th>
                <th className="px-2 py-1"></th>
                <th className="px-2 py-1"></th>
              </tr>
            </thead>
            <tbody>
              {filteredToday.length === 0 && (
                <tr>
                  <td colSpan={5} className="text-center text-gray-400 py-4">   </td>
                </tr>
              )}
              {filteredToday.map(r => (
                <tr key={r.id} className="hover:bg-blue-50 transition">
                  <td className="px-2 py-1">{r.guest_name}</td>
                  <td className="px-2 py-1">{r.guest_phone}</td>
                  <td className="px-2 py-1">{r.room_label}</td>
                  <td className="px-2 py-1">{statusBadge(r.status)}</td>
                  <td className="px-2 py-1">
                    <button
                      className="bg-blue-500 text-white rounded px-3 py-1 text-xs shadow hover:bg-blue-600 transition"
                      disabled={!shiftOpen}
                      title={shiftOpen ? '' : '  '}
                      onClick={() => alert('   ')}
                    >
                      <BadgeDollarSign className="inline w-4 h-4" />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Modals */}
      {showManagerHandoverModal && managerHandoverData && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-lg w-full max-w-lg p-6" dir="rtl">
            <h3 className="text-lg font-bold mb-2">   </h3>
            <div className="mb-2 text-sm"> : <span className="font-bold">{managerHandoverData.staffName}</span></div>
            <div className="mb-2 text-sm"> : <span className="font-bold">{currentShift?.short_code ? `#${currentShift.short_code}` : managerHandoverData.shiftId}</span></div>
            <div className="mb-2 text-sm">    : <span className="font-bold">{managerHandoverData.totalCashIncome} .</span></div>
            <div className="mb-2 text-sm">  : <span className="font-bold">{managerHandoverData.totalCashExpense} .</span></div>
            <div className="mb-2 text-sm"> : <span className="font-bold">{managerHandoverData.netCash} .</span></div>
            <div className="mb-2 text-sm">    : <span className="font-bold">{managerHandoverData.handoverInTotal} .</span></div>
            <div className="mb-2 text-sm">    : <span className="font-bold">{managerHandoverData.handoverOutTotal} .</span></div>
            <div className="mb-2 text-sm">   ( ): <span className="font-bold">{managerHandoverData.totalEIncome} .</span></div>
            <div className="mb-2 text-sm">   : <span className="font-bold">{managerHandoverData.closingCash} .</span></div>
            <div className="mb-4">
              <label className="block text-xs text-gray-600 mb-1">    </label>
              <input ref={managerHandoverAmountRef} className="w-full border rounded px-2 py-1" type="number" defaultValue={managerHandoverData.closingCash} />
            </div>
            <div className="flex justify-end gap-2">
              <button className="bg-gray-200 px-3 py-1 rounded" onClick={() => setShowManagerHandoverModal(false)}></button>
              <button className="bg-blue-600 text-white px-3 py-1 rounded" onClick={async () => {
                const actual = Math.round(Number(managerHandoverAmountRef.current.value || 0));
                //    DB
                await supabase.from('reception_shift_handovers').update({ status: 'received_by_manager', received_by: managerHandoverData.managerId, received_at: new Date().toISOString() }).eq('from_shift_id', managerHandoverData.shiftId).eq('to_manager_id', managerHandoverData.managerId);
                const { data: handRows } = await supabase.from('reception_shift_handovers').select('id').eq('from_shift_id', managerHandoverData.shiftId).eq('to_manager_id', managerHandoverData.managerId).order('created_at', { ascending: false }).limit(1);
                const handId = handRows && handRows[0] ? handRows[0].id : null;
                //    
                await supabase.from('accounting_transactions').insert({
                  direction: 'expense',
                  amount: actual,
                  payment_method: 'cash',
                  tx_date: new Date().toISOString().slice(0, 10),
                  reception_shift_id: managerHandoverData.shiftId,
                  created_by: managerHandoverData.managerId,
                  description: `    ${managerHandoverData.staffName}  ${managerHandoverData.shiftId}`,
                  delivered_in_handover_id: handId,
                });
                //        
                await supabase
                  .from('accounting_transactions')
                  .update({ status: 'confirmed', confirmed_at: new Date().toISOString(), confirmed_by: managerHandoverData.managerId, delivered_in_handover_id: handId })
                  .eq('reception_shift_id', managerHandoverData.shiftId)
                  .eq('payment_method', 'cash')
                  .eq('status', 'pending');
                setShowManagerHandoverModal(false);
                window.dispatchEvent(new Event('accounting-tx-updated'));
                alert('      .');
              }}>  </button>
            </div>
          </div>
        </div>
      )}
      {showExpense && (
        <AccountingExpenseModal onClose={() => setShowExpense(false)} />
      )}
      {showPendingReceiptModal && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-lg w-full max-w-lg p-6" dir="rtl">
            <h3 className="text-lg font-bold mb-2">   </h3>
            <p className="text-sm text-gray-700 mb-3">    <span className="font-bold">{pendingTotal} .</span>    .      .</p>
            <div className="mb-3 max-h-48 overflow-auto border rounded p-2">
              {(pendingReceipts || []).map(p => (
                <div key={p.id} className="flex justify-between items-center py-2 border-b last:border-b-0">
                  <div>
                    <div className="text-sm">: <span className="font-bold">{p.amount} .</span></div>
                    <div className="text-xs text-gray-500"> : <span className="font-medium">{p.sender_name}</span></div>
                  </div>
                </div>
              ))}
            </div>
            <div className="mb-3 grid grid-cols-1 md:grid-cols-2 gap-3">
              <div>
                <label className="block text-xs text-gray-600 mb-1"> </label>
                <div className="w-full border rounded px-2 py-1 bg-gray-50">{pendingTotal} .</div>
              </div>
              <div>
                <label className="block text-xs text-gray-600 mb-1">  </label>
                <input className="w-full border rounded px-2 py-1" type="number" step="1" value={actualReceivedAmount} onChange={e => setActualReceivedAmount(Math.round(Number(e.target.value || 0)))} />
              </div>
            </div>
            <div className="text-xs text-gray-600 mb-3">
              : <span className="font-bold">{Math.round((actualReceivedAmount || 0) - (pendingTotal || 0))} .</span>  {((actualReceivedAmount || 0) - (pendingTotal || 0)) === 0 ? '' : ((actualReceivedAmount || 0) - (pendingTotal || 0)) < 0 ? '' : ''}
            </div>
            <div className="flex justify-end gap-2">
              <button className="bg-gray-200 px-3 py-1 rounded" onClick={() => setShowPendingReceiptModal(false)} disabled={handoverLoading}></button>
              <button className="bg-blue-600 text-white px-3 py-1 rounded" onClick={confirmPendingReceipts} disabled={handoverLoading}>{handoverLoading ? '...' : ' '}</button>
            </div>
          </div>
        </div>
      )}
      {showHandoverModal && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-lg w-full max-w-md p-6" dir="rtl">
            <h3 className="text-lg font-bold mb-3">    </h3>
            <div className="mb-3 text-sm text-gray-700">      .</div>
            <div className="mb-3">
              <label className="block text-xs text-gray-600 mb-1"></label>
              <div className="flex gap-3">
                <label className="inline-flex items-center gap-2"><input type="radio" name="handoverType" checked={handoverType==='manager'} onChange={() => setHandoverType('manager')} /> </label>
                <label className="inline-flex items-center gap-2"><input type="radio" name="handoverType" checked={handoverType==='staff'} onChange={() => setHandoverType('staff')} />   </label>
              </div>
            </div>
            <div className="mb-3">
              <label className="block text-xs text-gray-600 mb-1"></label>
              {handoverType === 'manager' ? (
                <select className="w-full border rounded px-2 py-1" value={handoverRecipientId || ''} onChange={e => setHandoverRecipientId(e.target.value)}>
                  <option value="">--   --</option>
                  {handoverManagers.map(m => (
                    <option key={m.id} value={m.id}>{m.full_name}</option>
                  ))}
                </select>
              ) : (
                <select className="w-full border rounded px-2 py-1" value={handoverRecipientId || ''} onChange={e => setHandoverRecipientId(e.target.value)}>
                  <option value="">--    --</option>
                  {handoverStaffList.map(s => (
                    <option key={s.id} value={s.id}>{s.full_name}</option>
                  ))}
                </select>
              )}
            </div>
            <div className="mb-4">
              <label className="block text-xs text-gray-600 mb-1"></label>
              <input className="w-full border rounded px-2 py-1" type="number" step="1" value={handoverAmount} onChange={e => setHandoverAmount(Number(e.target.value))} />
            </div>
            <div className="flex justify-end gap-2">
              <button className="bg-gray-200 px-3 py-1 rounded" onClick={() => { setShowHandoverModal(false); }}>{handoverLoading ? '...' : ''}</button>
              <button className="bg-blue-600 text-white px-3 py-1 rounded" onClick={submitHandoverAndClose} disabled={handoverLoading}>{handoverLoading ? '...' : '  '}</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
