export const STAFF_ROLES = [
  {
    id: 'manager',
    label: ' ',
    description: '      .',
  },
  {
    id: 'assistant_manager',
    label: ' ',
    description: '      .',
  },
  {
    id: 'reception',
    label: '',
    description: '      .',
  },
  {
    id: 'housekeeping',
    label: '  (Housekeeping)',
    description: '      .',
  },
];

export const getRoleLabel = (id) => {
  const r = STAFF_ROLES.find(x => x.id === id);
  return r ? r.label : '';
};
