import React, { useEffect, useState, useContext } from 'react';
import { supabase } from '../supabaseClient';
import { AuthContext } from '../App.jsx';
import { ensureOpenShift } from '../utils/checkShift';

export default function GroupPaymentModal({ show, onClose, groupRows = [], currentUser, onDone }) {
  const [totalAmount, setTotalAmount] = useState(0);
  const [selectedIds, setSelectedIds] = useState([]);
  const [mode, setMode] = useState('per_remaining'); // 'equal' | 'per_remaining'
  const [method, setMethod] = useState('cash');
  const [txDate] = useState(() => new Date().toISOString().slice(0,10));
  const [loading, setLoading] = useState(false);
  const auth = useContext(AuthContext);

  useEffect(()=>{
    if (show) {
      setSelectedIds(groupRows.map(r => r.id));
      setTotalAmount(groupRows.reduce((s,r)=>s + (Number(r.remaining_amount || r.total_amount || 0)), 0));
      setMode('per_remaining');
      setMethod('cash');
    }
  }, [show, groupRows]);

  if (!show) return null;

  const toggle = (id) => {
    setSelectedIds(prev => {
      const s = new Set(prev || []);
      if (s.has(id)) s.delete(id); else s.add(id);
      return Array.from(s);
    });
  };

  const submit = async () => {
    try {
      //    
      try {
        const ok = await ensureOpenShift(auth);
        if (!ok) { window.alert('      .'); return; }
      } catch (_) { window.alert('    .'); return; }

      if (!selectedIds || selectedIds.length === 0) return alert('    ');
      const total = Math.round(Number(totalAmount) || 0);
      if (total <= 0) return alert('  ');
      setLoading(true);

      //     RPC   
      const params = {
        p_staff_user_id: currentUser?.id || auth?.id || null,
        p_tx_date: txDate || new Date().toISOString().slice(0,10),
        p_payment_method: method,
        p_total_amount: total,
        p_reservation_ids: selectedIds,
        p_distribution: mode,
      };
      const { error } = await supabase.rpc('apply_group_payment', params);
      if (error) {
        const msg = error?.message || error?.details || '   .';
        alert(msg);
        setLoading(false);
        return;
      }
      //  
      try { window.dispatchEvent(new Event('accounting-tx-updated')); } catch (_) {}
      onDone && onDone();
      onClose && onClose();
    } catch (e) {
      console.error('Group payment failed', e);
      alert('   : ' + (e.message || e));
    } finally { setLoading(false); }
  };

  return (
    <div className="fixed inset-0 bg-black/40 z-50 flex items-center justify-center p-4" dir="rtl">
      <div className="bg-white rounded shadow-lg w-full max-w-xl max-h-[90vh] overflow-auto">
        <div className="flex items-center justify-between p-4 border-b">
          <h3 className="font-bold"> </h3>
          <button className="text-gray-500" onClick={onClose}></button>
        </div>
        <div className="p-4 space-y-3">
          <div className="grid grid-cols-2 gap-2 items-center">
            <label className="text-sm"> </label>
            <input type="number" step="1" className="border rounded px-2 py-1" value={totalAmount} onChange={e=>setTotalAmount(e.target.value)} />
          </div>

          <div>
            <div className="text-sm font-medium mb-2">    </div>
            <div className="max-h-48 overflow-auto border rounded p-2 text-sm">
              {groupRows && groupRows.length ? groupRows.map(r => (
                <label key={r.id} className="flex items-center gap-2 mb-1">
                  <input type="checkbox" checked={selectedIds.includes(r.id)} onChange={()=>toggle(r.id)} />
                  <span>{r.room_label || r.room_id}  : {r.remaining_amount ?? r.total_amount}</span>
                </label>
              )) : <div className="text-gray-500">     .</div>}
            </div>
          </div>

          <div>
            <div className="text-sm font-medium mb-1"> </div>
            <label className="flex items-center gap-2"><input type="radio" name="mode" checked={mode==='per_remaining'} onChange={()=>setMode('per_remaining')} />   </label>
            <label className="flex items-center gap-2"><input type="radio" name="mode" checked={mode==='equal'} onChange={()=>setMode('equal')} />  </label>
          </div>

          <div>
            <div className="text-sm font-medium mb-1"> </div>
            <select className="border rounded px-2 py-1" value={method} onChange={e=>setMethod(e.target.value)}>
              <option value="cash"> ()</option>
              <option value="instapay"> /  </option>
              <option value="other"> </option>
            </select>
          </div>

          <div className="flex items-center justify-end gap-2 pt-2">
            <button className="px-3 py-1 border rounded" onClick={onClose} disabled={loading}></button>
            <button className="px-3 py-1 bg-emerald-600 text-white rounded" onClick={submit} disabled={loading}>{loading?'...':' '}</button>
          </div>
        </div>
      </div>
    </div>
  );
}
